import React, { useState } from 'react';
import Cookies from 'universal-cookie';
import axios from 'axios';

import signinImage from '../assets/signup.jpg'

const Auth = () => {

    const [isSignup, setIsSignup] = useState(true);

    const handleChange = () => { }

    const switchMode = () => {
        setIsSignup((prevIsSignup) => !prevIsSignup)

        document.querySelector('.auth__form-container_fields-content').classList.toggle('active')

        // if(setIsSignup !== true){

        //     document.querySelector('.auth__form-container_fields-content').classList.toggle('active')
        // } else if(setIsSignup == true) {

        //     document.querySelector('.auth__form-container_fields-content').classList.toggle('active2')
        // }
        
    }

    return (
        <div className="auth__form-container">
            <div className="auth__form-container_fields">
                <div className="auth__form-container_fields-content">
                    <p>{isSignup ? '' : ''}</p>
                    <form onSubmit={() => { }}>
                        {isSignup && (
                            <div className="auth__form-container_fields-content_input">
                                <label htmlFor="fullName"></label>
                                <input
                                    name="fullName"
                                    type="text"
                                    placeholder=""
                                    onChange={handleChange}
                                    required
                                />
                            </div>
                        )}
                        <div className="auth__form-container_fields-content_input">
                            <label htmlFor="username"></label>
                            <input
                                name="username"
                                type="text"
                                placeholder=""
                                onChange={handleChange}
                                required
                            />
                        </div>
                        {isSignup && (
                            <div className="auth__form-container_fields-content_input">
                                <label htmlFor="phoneNumber"></label>
                                <input
                                    name="phoneNumber"
                                    type="text"
                                    placeholder=""
                                    onChange={handleChange}
                                    required
                                />
                            </div>
                        )}
                        {isSignup && (
                            <div className="auth__form-container_fields-content_input">
                                <label htmlFor="avatarURL"> URL</label>
                                <input
                                    name="avatarURL"
                                    type="text"
                                    placeholder=" URL"
                                    onChange={handleChange}
                                    required
                                />
                            </div>
                        )}
                            <div className="auth__form-container_fields-content_input">
                                <label htmlFor="password"></label>
                                <input
                                    name="password"
                                    type="password"
                                    placeholder=""
                                    onChange={handleChange}
                                    required
                                />
                            </div>
                        {isSignup && (
                            <div className="auth__form-container_fields-content_input">
                                <label htmlFor="confirmpassword"></label>
                                <input
                                    name="confirmpassword"
                                    type="password"
                                    placeholder=""
                                    onChange={handleChange}
                                    required
                                />
                            </div>
                        )}
                    </form>
                    <div className="auth__form-container_fields-account">
                        <p>
                            {isSignup
                                ? "  ??"
                                : " ?"
                            }
                            <span onClick={switchMode}>
                                {isSignup
                                    ? ''
                                    : ''
                                }
                            </span>
                        </p>
                    </div>
                </div>
            </div>
            <div className="auth__form-container_image">
                <img src={signinImage} alt="#" />
            </div>
        </div>
    )
}

export default Auth